import {enginePagesConfig} from "./Config";

window.onload = () =>
{
    const url = window.location.search
    const queryParams = new URLSearchParams(url)
    let engine = queryParams.get("engine");
    if(!engine) 
        return
    console.log(engine)
    //open the page for the engine
    window.location.href = `${engine}.html`
    
    
}